import { BaseMidiRouteInput } from "./MidiRoutePreset/BaseMidiRouteInput";
import * as Connection from "../connection";
import { MidiRouterChain } from "./MidiRoutePreset/MidiRouterChain"
import { MIDI_FILTER_ACTION_IF_NOT } from "../channel/WcMidiIn"


export class KeyboardZone {
  splitPosition: number;
  constructor(splitPosition: number) {
    this.splitPosition = splitPosition;
  }

  getPositionName(n: number) {
    const notes = ["C", "C#", "D", "D#", "E", "F", "F#", "G", "G#", "A", "A#", "B"];
    const inputVal = n
    const noteName = notes[inputVal % 12]
    const octave = Math.floor(Math.floor(inputVal) / 12 - 1);
    return `${octave} ${noteName}`
  }

  get positionName() {
    return this.getPositionName(this.splitPosition);
  }
}


export class ZoneRange {
  name: string
  fromKey: number
  toKey: number
  constructor(name: string, fromKey: number, toKey: number) {
    this.name = name;
    this.fromKey = fromKey
    this.toKey = toKey
  }
}


export enum EventTypes {
  noteoff = 8,
  noteon = 9,
  keyaftertouch = 10,
  controlchange = 11,
  channelmode = 11,
  nrpn = 100,
  programchange = 12,
  channelaftertouch = 13,
  pitchbend = 14
}

export enum DropdownMidiEventTypeEnum {
  allEvents = 0,
  noteOnOf = 1,
  noteoff = 2,
  noteon = 3,
  keyaftertouch = 4,
  controlchange = 5,
  channelmode = 6,
  nrpn = 7,
  programchange = 8,
  channelaftertouch = 9,
  pitchbend = 10
}
export const dropdownMidiEventType = [
  { id: DropdownMidiEventTypeEnum.allEvents, eventTypes: [], name: "-" },
  { id: DropdownMidiEventTypeEnum.noteOnOf, eventTypes: [EventTypes.noteon, EventTypes.noteoff], name: "Note on/off" },
  { id: DropdownMidiEventTypeEnum.noteoff, eventTypes: [EventTypes.noteoff], name: "Note off" },
  { id: DropdownMidiEventTypeEnum.noteon, eventTypes: [EventTypes.noteon], name: "Note on" },
  { id: DropdownMidiEventTypeEnum.keyaftertouch, eventTypes: [EventTypes.keyaftertouch], name: "keyaftertouch" },
  { id: DropdownMidiEventTypeEnum.controlchange, eventTypes: [EventTypes.controlchange], name: "controlchange" },
  //{ id: DropdownMidiEventTypeEnum.channelmode, eventTypes: [EventTypes.channelmode], name: "channelmode" },
  { id: DropdownMidiEventTypeEnum.nrpn, eventTypes: [EventTypes.nrpn], name: "nrpn" },
  { id: DropdownMidiEventTypeEnum.programchange, eventTypes: [EventTypes.programchange], name: "programchange" },
  { id: DropdownMidiEventTypeEnum.channelaftertouch, eventTypes: [EventTypes.channelaftertouch], name: "channelaftertouch" },
  { id: DropdownMidiEventTypeEnum.pitchbend, eventTypes: [EventTypes.pitchbend], name: "pitchbend" },
]

class EasyConfigRoute extends BaseMidiRouteInput {
  constructor(midiInputName: string) {
    super(midiInputName)
  }



  splitRangeId = -1
  fromSelectedMidiEventTypeId = 0;
  fromChannel = -1
  fromData1 = -1
  transpose = 0

  fromCcOrNrpnStart = 0
  fromCcOrNrpnEnd = 127
  toCcOrNrpnStart = 0
  toCcOrNrpnEnd = 127

  toSelectedMidiEventTypeId = 0;
  toChannel = -1
  toData1 = -1

  toDestinationName = "";
  get toDestinationId() {
    return Connection.loginStatus.getMidiOutputIdByName(this.toDestinationName);
  }

  set toDestinationId(inputId: number) {
    this.toDestinationName = Connection.loginStatus.outPorts[inputId]
  }

  get getMidiRouterChains(): MidiRouterChain[] {
    // GGGG
    const ret: MidiRouterChain[] = [];
    const isAllValid = this.toDestinationId !== -1

    const inputZonesAndRoute: InputZonesAndRoutes = Connection.loginStatus.userDataConfig.activePreset.easyConfig.inputZonesAndRoutes[this.midiInputName]
    if (!isAllValid) { return ret; }


    let isAllDefault = true;
    let channelFilter = [[0, 16, 0]]

    if (this.fromChannel !== -1) {
      isAllDefault = false;
      channelFilter = [[this.fromChannel, this.toChannel]]
    }

    let eventFilter = [[0, 16, 0]]
    if (this.fromSelectedMidiEventTypeId !== 0) {
      isAllDefault = false
      if (this.fromSelectedMidiEventTypeId === this.toSelectedMidiEventTypeId && 
        this.toSelectedMidiEventTypeId === DropdownMidiEventTypeEnum.noteOnOf) {
        eventFilter = [[EventTypes.noteoff], [EventTypes.noteon]]
      }
      else {
        eventFilter = [[dropdownMidiEventType.filter( (row)=>{return row.id===this.fromSelectedMidiEventTypeId})[0].eventTypes[0],
        dropdownMidiEventType.filter( (row)=>{return row.id===this.toSelectedMidiEventTypeId})[0].eventTypes[0]]]
      }
    }


    let data1Filter = [[0, 127, 0]]
    const data2Filter = [[0, 127, 0]]
    if (this.isShowSplitRanges) {
      if (this.splitRangeId !== -1) {
        const zoneRange = inputZonesAndRoute.getZoneRanges[this.splitRangeId]
        data1Filter = [[zoneRange.fromKey, zoneRange.toKey, zoneRange.fromKey + this.transpose]]
      } else {
        data1Filter = [[0, 127, 0 + this.transpose]]
      }
    }

    if (this.isShowData1Filed && this.fromData1 !== -1) {
      if (this.toData1 === -1) {
        data1Filter = [[this.fromData1]]
      } else {
        data1Filter = [[this.fromData1, this.toData1]]
      }
    }

    if (this.isShowData1Filed && this.isShowToData1Filed &&
       this.fromData1 !== -1 && this.toData1 !== -1){
      data1Filter = [[this.fromData1, this.toData1]]
       }

    if (this.isShowData1Filed || this.isShowToData1Filed){       
      data2Filter[0][0] = this.fromCcOrNrpnStart
      data2Filter[0][1] = this.fromCcOrNrpnEnd     
      data2Filter[0][2] = this.toCcOrNrpnStart
      data2Filter[0][3] = this.toCcOrNrpnEnd 
   
    }    
    
    const midiRouterChain = new MidiRouterChain("Easyconfig");
    midiRouterChain.isEasyConfig = true
    if (!isAllDefault) {
      midiRouterChain.addFilterFilterAndTransform("EasyConfig", MIDI_FILTER_ACTION_IF_NOT.DELETE_IF_NOT,
        JSON.stringify(channelFilter),
        JSON.stringify(eventFilter),
        JSON.stringify(data1Filter),
        JSON.stringify(data2Filter))
    }

    const midiOutId = this.toDestinationId
    const midioutObj = new BaseMidiRouteInput(Connection.loginStatus.inPorts[midiOutId])

    midiRouterChain.addFilterMidiDestination(midioutObj);
    ret.push(midiRouterChain)

    return ret;
  }


  get isShowSplitRanges() {
    const currEventType = dropdownMidiEventType.filter(row => {return row.id ==this.fromSelectedMidiEventTypeId})[0]
    return currEventType.eventTypes.indexOf(EventTypes.noteoff) > -1 ||
    currEventType.eventTypes.indexOf(EventTypes.noteon) > -1
  }

  get isShowData1Filed() {
    const fromEventtTypeId = dropdownMidiEventType.filter( (row)=>{return row.id === this.fromSelectedMidiEventTypeId})[0].eventTypes[0]
    return [EventTypes.controlchange, EventTypes.nrpn].indexOf(fromEventtTypeId) > -1
  }

  get getData1Description() {
    const fromEventtTypeId = dropdownMidiEventType.filter( (row)=>{return row.id === this.fromSelectedMidiEventTypeId})[0].eventTypes[0]
    if ([EventTypes.controlchange, EventTypes.nrpn].indexOf(fromEventtTypeId) > -1) {
      return "cc number";
    }
    return ""
  }

  get isShowToData1Filed() {
    const toEventtTypeId = dropdownMidiEventType.filter( (row)=>{return row.id === this.toSelectedMidiEventTypeId})[0].eventTypes[0]
    // tue for cc
    return [EventTypes.controlchange, EventTypes.nrpn].indexOf(toEventtTypeId) > -1
  }

  get getToData1Description() {
    const toEventtTypeId = dropdownMidiEventType.filter( (row)=>{return row.id === this.toSelectedMidiEventTypeId})[0].eventTypes[0]
    if ([EventTypes.controlchange, EventTypes.nrpn].indexOf(toEventtTypeId) > -1) {
      return "cc number";
    }
    return ""
  }



}



export class InputZonesAndRoutes extends BaseMidiRouteInput {
  keyboardSplits: KeyboardZone[] = []
  zoneNames: string[] = []


  easyConfigRoutes: EasyConfigRoute[] = []

  constructor(inputName: string) {
    super(inputName)
  }



  addRoute() {
    const _ret = new EasyConfigRoute(this.midiInputName)
    this.easyConfigRoutes.push(_ret)
    return _ret
  }

  addKeyboardSplit(splitPosition: number) {
    const _ret = new KeyboardZone(splitPosition)
    this.keyboardSplits.push(_ret)
    this.doUpdateZoneNameLen();
    return _ret
  }

  doUpdateZoneNameLen() {
    if (this.keyboardSplits.length === 0) {
      this.zoneNames.length = 0;
      return;
    }
    this.zoneNames.splice(this.keyboardSplits.length + 1);
    for (let i = 0; i < this.keyboardSplits.length + 1; i++) {
      if (this.zoneNames[i] === undefined) { this.zoneNames[i] = `I${this.midiInputId}S${i}` }
    }
  }

  get getZoneRanges() {
    const zoneRanges: ZoneRange[] = []
    zoneRanges.length = 0;
    let newAry = this.keyboardSplits.map((row) => { return row.splitPosition; })


    // Begin and end of keyboard
    newAry.push(8)
    newAry.push(96)

    newAry.sort((a, b) => {
      return a - b
    })
    //uniq
    newAry = newAry.filter((v, i, a) => a.indexOf(v) === i);

    for (let i = 0; i < newAry.length - 1; i++) {
      zoneRanges.push(new ZoneRange(this.zoneNames[i], newAry[i], newAry[i + 1]))
    }

    return zoneRanges;
  }


}






export class EasyConfig {



  inputZonesAndRoutes: { [value: string]: InputZonesAndRoutes } = {}

  addRoute(inputName: string) {
    this.ensureEasyConfigInputExists(inputName);

    return this.inputZonesAndRoutes[inputName].addRoute();
  }

  addKeyboardSplit(inputName: string, splitPosition: number) {
    this.ensureEasyConfigInputExists(inputName)

    this.inputZonesAndRoutes[inputName].addKeyboardSplit(splitPosition)
  }

  ensureEasyConfigInputExists(intputName: string) {
    if (this.inputZonesAndRoutes[intputName] === undefined) {
      this.inputZonesAndRoutes[intputName] = new InputZonesAndRoutes(intputName)
    }
  }

}



