import * as Connection from "../connection";

export enum EventType {
    CC,
    PROGRAM_CHANGE,
    NRPN
}

export const eventTypes = [
{id: EventType.CC, name: "CC"},
{id: EventType.PROGRAM_CHANGE, name: "Program Change"},
{id: EventType.NRPN, name: "NRPN"} ]

export class UserControl {
    eventType: EventType = EventType.CC;
    description = "Description"
    inputVal = "0"
    minVal = "0"
    maxVal = "127"
    is64Mode = false
    isEditMode = true
    channelId = "1"
    ccId = "7"

    nrpnControl = "0"
    

    outputPortnName = "";
    get outputPortId(){
      return Connection.loginStatus.getMidiOutputIdByName(this.outputPortnName);
    }

    set outputPortId(inputId: number){
      this.outputPortnName = Connection.loginStatus.outPorts[inputId]
    }

    isShowDropdown = false
    dropdownListId = 0;

    get dropDownDecriptions() {
        const obj =  Connection.loginStatus.userDataConfig.dropdownlists[this.dropdownListId]
        if (obj === undefined){return "";}
        return obj.data
    }



    async doSend(){
        if (this.outputPortId === -1){return;}
        const port = await Connection.connection.wcmidiout.port(this.outputPortId )
        if (this.eventType === EventType.CC){
            await port.sendControlChange(parseInt(this.ccId),parseInt(this.inputVal),[parseInt(this.channelId)])
        }
        if (this.eventType === EventType.PROGRAM_CHANGE){
            await port.sendProgramChange(parseInt(this.inputVal),[parseInt(this.channelId)])
        }    
        
        if (this.eventType === EventType.NRPN){
            const nrpnDataMSB = parseInt(this.inputVal) >> 7
            const nrpnDataLSB = parseInt(this.inputVal) & 0x7F

            const nrpnControlMSB = parseInt(this.nrpnControl) >> 7
            const nrpnControlLSB = parseInt(this.nrpnControl) & 0x7F
            await port.setNonRegisteredParameter(
            [nrpnControlMSB,nrpnControlLSB],
            [nrpnDataMSB,nrpnDataLSB],
            [parseInt(this.channelId)]) 
        }    

    }
}